.recipe_recipe__2nOI7 {
    border-radius: 10px;
    box-shadow: 0px 2px 20px rgb(71,71,71);
    margin: 20px;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    background: white;
    align-items: center;
    min-width: 40%;
}
body {
  margin: 0;
  padding: 0;
}

.App {
  min-height: 100vh;
  background-image: -webkit-linear-gradient(330deg, #a1c4fd 0%, #c2e9fb 100%);
  background-image: linear-gradient(120deg, #a1c4fd 0%, #c2e9fb 100%);

}

.search-form {
  min-height: 10vh;
  display: flex;
  justify-content: center;
  align-items: center;
  
}

.search-bar {
  width: 50%;
  border: none;
  padding: 10px;
}

.search-button {
  background: #f08080;
  font-family: Arial, sans-serif;
  border: none;
  padding: 10px 20px;
  color: white;
  cursor:pointer;
  -webkit-transition: .2s;
  transition: .2s;
  
}

.search-button:hover {
  background: #f19191;
}
.recipes {
  display: flex;
  justify-content: space-around;
  flex-wrap: wrap;
}



